/* Variables
   ----------------------------- */

$break-mobile: max-width 600px;
$break-tablet: max-width 768px;

/* Imports
   ----------------------------- */

@import url(https://fonts.googleapis.com/css?family=Ubuntu:300,500,700);
@import "./node_modules/breakpoint-sass/stylesheets/breakpoint";
@import "partials/colors";
@import "partials/helpers";
@import "partials/normalize";
@import "partials/hover";

/* HTML
   ----------------------------- */

html {
  box-sizing: border-box;
  font-size: 62.5%; }

*, *:before, *:after {
  box-sizing: inherit; }

body {
  @include font-size(16);
  font-family: 'Ubuntu', sans-serif;
  // background: $black
  position: relative;
  background-color: rgb(255, 60, 31); }

.lt-ie9 body {
  margin-top: 5em; }

ul, section, body {
  margin: 0;
  padding: 0; }

a {
  text-decoration: none;
  // +link-colors($white, $primary)
  &:hover, &:focus {
    text-decoration: none; } }

a, input {
  transition: all .3s ease; }

a, input, button, textarea, select {
  &:active,
  &:focus {
    box-shadow: none;
    outline: none; } }

p {
  line-height: 1.5; }

hr {
  margin: 2em 0;
  height: 1px;
  border: none;
  background: #404040; }

/* Styles
   ----------------------------- */

.ion-1 {
  font-size: 1em; }

.ion-2 {
  font-size: 2em;
  line-height: 1.4; }

.main-nav {
  display: block;
  text-align: center;
  width: 100%;
  position: absolute;
  bottom: 5em;
  margin: auto;
  @include breakpoint($break-mobile) {
    position: initial;
    width: 80%;
    margin-bottom: 2em;
    .button {
      margin-bottom: 1em; } } }

.top {
  display: block; }

.small {
  text-transform: uppercase;
  font-weight: bold;
  @include font-size(13);
  color: $white-dark; }

.side {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  width: 30%;
  background: $black;
  overflow-y: auto;
  @include breakpoint($break-mobile) {
    width: 100%; } }

.side, .timeline li {
  &:before {
    position: absolute;
    left: 24px;
    top: 0;
    content: ' ';
    display: block;
    width: 2px;
    height: 100%;
    margin-left: -3px;
    background: #444;
    z-index: 5; } }

.main {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  background: $black url('../img/3-dark2.jpg') top center no-repeat;
  //+background-size(cover)
  z-index: 20;
  transition: transform .3s ease;
  box-shadow: 0 0 10px $black-darker;
  overflow: hidden;
  @include breakpoint($break-mobile) {
    text-align: center;
    overflow: auto; } }

.work {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 70%;
  z-index: 21;
  overflow-x: hidden;
  display: none;
  @include breakpoint($break-mobile) {
    overflow-x: hidden;
    width: 100%; }
  a {
    color: $black-light; } }

.work-info {
  h3 {
    text-align: center;
    font-size: 2em;
    margin: 1em; }
  h4 {
    margin: 0 32px 8px;
    font-size: 1.5em; }
  iframe:not(.cp_embed_iframe) {
    height: 447px; }
  iframe {
    width: 100%;
    margin: 1em auto;
    @include breakpoint($break-mobile) {
      height: 56vw; } }
  hr {
    background: #EAEAEA; }
  img {
    display: block;
    width: 100%;
    height: auto;
    margin-bottom: 32px; }
  p {
    margin: 0 32px 32px;
    line-height: 1.5;
    em {
      display: block;
      padding: 1em 2em;
      @include breakpoint($break-mobile) {
        padding: 0; } } }
  a {
    color: $primary;
    &:hover {
      text-decoration: underline; }
    @extend .fancy-hover; } }

.intro {
  padding: 3em;
  color: $white;
  width: 100%;
  position: relative;
  top: 30%;
  text-align: center;
  transform: translateY(-50%);
  @include breakpoint($break-mobile) {
    transform: none;
    top: auto;
    padding: 1em 3em; }
  hr {
    display: block;
    width: 100%;
    max-width: 300px;
    margin: 2em auto;
    opacity: .25;
    border-top: 2px solid white;
    @include breakpoint($break-mobile) {
      display: none; } }
  .adjectives {
    position: relative;
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 6px;
    line-height: 100px;
    @include font-size(102);
    text-shadow(0 0 10px $black) {}
    @include breakpoint($break-mobile) {
      letter-spacing: normal;
      line-height: normal;
      font-size: 3em;
      font-size: 11vw; } } }

.timeline {
  position: relative;
  width: 100%;
  margin: 0 auto;
  padding: 1em 0;
  list-style-type: none;
  li {
    padding: 1em 0 1em 2em;
    cursor: pointer;
    @extend .animate;
    &:hover {
      background: $black-light; }
    &:after {
      content: "";
      display: block;
      height: 0;
      clear: both;
      visibility: hidden; }
    &.active {
      background: $black-dark; } } }

.direction-r {
  position: relative;
  width: 100%;
  float: right; }

.flag {
  position: relative;
  display: inline-block;
  padding: 0 1em;
  border-radius: 5px;
  font-weight: 600;
  text-align: left;
  color: $white;
  margin: 0;
  @include font-size(16);
  &:before {
    position: absolute;
    top: 60%;
    left: -16px;
    content: ' ';
    display: block;
    width: 12px;
    height: 12px;
    margin-top: -10px;
    background: $black-dark;
    border-radius: 10px;
    border: 2px solid $primary;
    box-shadow: 0 0 0 3px #444;
    z-index: 10; } }

.desc {
  margin: 1em 1em 0;
  color: $white;
  @include font-size(13);
  font-style: italic;
  line-height: 1.5em;
  color: $white-dark; }

.lt-ie9 .work-container {
  overflow-y: scroll; }

.work-piece {
  position: absolute;
  width: 90%;
  height: 90%;
  left: 100%;
  right: 0;
  top: 5%;
  max-width: 800px;
  background: $white-lighter;
  padding: 0;
  margin: 0 auto;
  list-style: none;
  z-index: 30;
  overflow-y: auto;
  display: none;
  @include breakpoint($break-mobile) {
    width: 100%;
    height: 100%;
    top: 0; }
  &.active {
    display: block; }
  .button {
    border: 1px solid $white-dark;
    &:hover {
      background: $primary;
      color: $white;
      border: 1px solid $primary-dark; } } }

.buttons {
  padding: 1em; }

.prev, .next, .backtotop {
  padding: 0;
  z-index: 10;
  &.ion-1 {
    font-size: 1.4em;
    line-height: 1.8; } }

.prev {
  left: 1em; }

.next {
  right: 1em; }

.backtotop.button {
  display: inline-block; }

.close, .prev, .next, .backtotop {
  width: 44px;
  height: 44px;
  display: block;
  bottom: 1em;
  text-align: center;
  &:before {
    line-height: 41px; } }

.close {
  position: absolute;
  right: 15px;
  top: 0;
  color: $black-light;
  background: $white-lighter;
  z-index: 30;
  display: none;
  &:hover {
    color: $white;
    background: $primary; } }

.checklink {
  width: auto;
  @include breakpoint($break-mobile) {
    width: 100%;
    margin-bottom: 4em; } }

.load {
  position: absolute;
  top: .8em;
  left: 49%;
  width: 38px;
  height: 38px;
  background: url('../img/loading.svg') center center no-repeat;
  // background: url('../img/loader2.gif') center center no-repeat
  display: none;
  @include breakpoint($break-mobile) {
    left: .8em; } }

.helper {
  position: absolute;
  padding: 7px 7px 1em;
  right: 2em;
  top: 20%;
  z-index: 40;
  background: $black;
  border-radius: 25px;
  opacity: 0;
  transform: translate3d(120%, 0, 0);
  transition: all .2s ease-in-out;
  &.helper-active {
    opacity: 1;
    transform: translate3d(0%, 0, 0); } }

.helper-note {
  display: block;
  padding: 10px 10px 10px 1em;
  background: $white;
  border-radius: 50px;
  @include breakpoint($break-mobile) {
    display: none; }
  span {
    padding: 7px 8px;
    border-radius: 100%;
    margin-left: .5em; } }

.legend {
  display: inline-block;
  margin: .5em 0 0 1em;
  list-style: none;
  color: white;
  line-height: 2;
  li:before {
    content: '';
    display: inline-block;
    margin-right: 10px;
    width: 12px;
    height: 12px;
    background: transparent;
    border-width: 2px;
    border-style: solid;
    border-radius: 50%; } }

.about-me {
  margin-right: 1em;
  background: none;
  border: 1px solid $white;
  color: $white;
  @include breakpoint($break-mobile) {
    margin: 0; } }

.about-me-details {
  font-weight: normal;
  a {
    border-bottom: 1px dotted $primary;
    color: $white;
    // &:hover
 } }    //   color: $primary-lighter

#checkout {
  color: $black-light;
  @include breakpoint($break-mobile) {
    width: auto; } }

.about-me, #checkout {
  &:hover {
    background: $primary;
    border-color: $primary;
    color: $white;
    opacity: 1; }
  @include breakpoint($break-mobile) {
    display: block; } }

.intro .button {
  margin-top: 20%; }

.about {
  z-index: 30;
  display: none;
  opacity: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  background: $black;
  background: transparentize($black, .05);
  padding: 0;
  overflow-y: auto;
  @include font-size(40);
  color: $white;
  text-align: center;
  @include breakpoint($break-mobile) {
    @include font-size(30);
    padding: 20px 20px 0; } }

.about-close {
  color: $white;
  position: fixed;
  right: 2em;
  top: 1em;
  display: block;
  padding: 0 .8em;
  @include font-size(20);
  background: black;
  border-radius: 50%;
  &:hover {
    background: $primary; } }

.social {
  position: absolute;
  bottom: 1em;
  right: 1em;
  @include breakpoint($break-mobile) {
    left: .2em;
    bottom: .2em;
    display: none; }
  a {
    @include font-size(20);
    color: $white;
    position: relative;
    background: transparentize($black, .2);
    display: block;
    padding: 15px 18px;
    margin: .5em .5em 0 0;
    width: 56px;
    text-align: center;
    border: 1px solid transparent;
    border-radius: 50px;
    @include breakpoint($break-mobile) {
      margin: .2em .2em 0 0; }
    &:hover {
      color: $primary;
      border: 1px solid $primary; } } }

.logo {
  display: inline-block;
  width: 100px;
  @include breakpoint($break-mobile);
  svg {
    fill: $white; }
  path {
    fill: inherit; } }

.about-container {
  @include font-size(16);
  display: block;
  margin: 4em auto;
  padding: 1em;
  width: 100%;
  max-width: 600px;
  @include breakpoint($break-mobile) {
    margin: 0 auto;
    padding: 0; } }

.about-info {
  text-align: center; }

.about-picture {
  position: relative;
  display: block;
  margin: 0 auto;
  width: 140px;
  height: 140px;
  background: url('../img/leo.jpg') center center no-repeat;
  border-radius: 100%;
  background-size: contain;
  &:after {
    position: absolute;
    content: '';
    height: 100%;
    width: 100%;
    top: 0%;
    left: 0%;
    border-radius: 50%;
    z-index: -1;
    box-shadow: 0 0 15px $primary;
    animation: glow-grow 2s ease-out infinite; }
  &:before {
    content: '';
    position: absolute;
    left: 0;
    width: 140px;
    height: 140px;
    background: url('../img/avatar.jpg') center center no-repeat;
    background-size: contain;
    border-radius: 100%;
    opacity: 1;
    transition: opacity 0.3s ease; }
  &:hover:before {
    opacity: 0; } }

@keyframes glow-grow {
  0% {
    opacity: 0;
    transform: scale(0); }
  70% {
    opacity: 1; }
  100% {
    transform: scale(1.2);
    opacity: 0; } }

.about-name {
  font-weight: normal;
  @include font-size(25);
  margin: .5em 0 1em;
  display: block; }

.about-details {
  margin-bottom: 1em;
  li {
    width: 50%;
    padding: 0 .5em;
    list-style-type: none;
    text-align: left;
    margin: 1em 0;
    font-weight: normal;
    @include breakpoint($break-mobile) {
      width: 100%; }
    &.odd {
      float: left; }
    &.even {
      float: right; }
    span {
      margin-bottom: 5px;
      text-transform: uppercase;
      font-weight: bold;
      display: block;
      color: $white-darker;
      @include font-size(11); }
    a {
      color: $white;
      @extend .fancy-hover; } } }

.at:after {
  content: '@'; }
.dot:after {
  content: '.'; }

.demo-reel {
  position: relative;
  top: 50%;
  display: block;
  text-align: center;
  transform: translateY(-50%);
  iframe {
    max-width: 810px;
    width: 80%;
    @include breakpoint($break-mobile) {
      height: auto; } } }

.bg {
  position: absolute;
  top: 0;
  bottom: 0;
  width: 100%;
  z-index: 22;
  background: rgba(black, .75);
  display: none; }

.graph-bg {
  height: 76px;
  width: 100%;
  background: url(../img/data.gif) 0 0 repeat-x;
  opacity: .25; }

.made-with-love {
  position: absolute;
  bottom: 2em;
  left: 2em;
  color: white;
  opacity: .5;
  font-size: 0.7em;
  text-transform: uppercase;
  @include breakpoint($break-mobile) {
    position: initial; }
  .ion-heart {
    color: red; }
  a {
    color: $white;
    &:hover {
      color: $primary; } } }

.typed-cursor {
  opacity: .5;
  animation: blink .7s infinite;
  @include font-size(102);
  @include breakpoint($break-mobile) {
    font-size: 3em;
    line-height: normal; } }

@keyframes blink {
  0% {
      opacity: .5; }
  50% {
      opacity: 0; }
  100% {
      opacity: .5; } }

.tippy-tooltip {
  font-size: .9em !important; }

#particles-js {
  position: fixed;
  width: 100%;
  height: 100%;
  z-index: -1; }

/* Color Labels
   ----------------------------- */

.web:before {
  border-color: $blue; }
.design:before {
  border-color: $yellow; }
.print:before {
  border-color: $teal; }
.animation:before {
  border-color: $purple; }
.industrial:before {
  border-color: $primary; }
.none:before {
  border-color: $white; }
